.App{
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Montserrat', sans-serif;
}

.title{
  text-align: center;
  font-size: 25px;
}

input{
  width: 50vw;
  border-style: none none solid;
  border-width: 2px;
  border-color: #000;
  border-radius: 0px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 900;
  font-size: 36px;
}
.form{
  margin-top: 100px;
  padding: 5;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
}
.list-item{
  display: inline-block;
}
ul{
  list-style-type: none;
}
li{
  width: 55vw;
  height: auto;
  border: 2px solid #000;
  border-radius: 20px;
  padding: 20px;
  margin-top: 5px;

}

button{
  padding-top: 9px;
  padding-right: 14px;
  padding-left: 14px;
  padding-bottom: 9px;
  border-radius: 100%;
  background-color: #000;
  border: 0;
  color: white;
  cursor: pointer;
  font-weight: 900;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
button:hover{
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}


.delete-button{
  float: right;
  padding-left: 5px;
  margin-top:-10px;

}
@media (max-width: 767px) {
  input{
    font-size: 24px;
  }
}
@media (max-width: 479px) {
  input{
    font-size: 16px;
  }
}



